import React from 'react';
import PageHeader from './components/PageHeader';

import aboutBg from '../../images/about-bg.jpg';

/* 404 page */
const NotFound = () => {
  return (
    <div>
      <PageHeader 
        heading="Page Not Found"
        subheading="Perhaps this page was lost at the sea"
        backgroundImage={{backgroundImage: `url(${aboutBg})`}} />

      <div className="container">
        <h1 className="section-heading">404</h1>
        <p>Page not found.</p>
        <hr />
      </div>
    </div>
  )
}

export default NotFound



